"use client"

import { useUser } from "@/contexts/user-context"
import { useRouter } from "next/navigation"
import { useEffect } from "react"
import { Package, CheckCircle, Clock, Truck } from "lucide-react"

export default function OrdersPage() {
  const { user, isLoggedIn, orders } = useUser()
  const router = useRouter()

  useEffect(() => {
    if (!isLoggedIn) {
      router.push("/login")
    }
  }, [isLoggedIn, router])

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "delivered":
        return <CheckCircle className="text-green-600" size={20} />
      case "shipped":
        return <Truck className="text-blue-600" size={20} />
      case "processing":
        return <Clock className="text-yellow-600" size={20} />
      default:
        return <Package className="text-gray-600" size={20} />
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "delivered":
        return "bg-green-50 border-green-200"
      case "shipped":
        return "bg-blue-50 border-blue-200"
      case "processing":
        return "bg-yellow-50 border-yellow-200"
      default:
        return "bg-gray-50 border-gray-200"
    }
  }

  if (!user) return null

  return (
    <div className="min-h-screen bg-background">
      <div className="max-w-4xl mx-auto px-4 py-12">
        <h1 className="text-3xl font-bold text-foreground mb-8">My Orders</h1>

        {orders.length === 0 ? (
          <div className="bg-card rounded-lg border border-border p-12 text-center">
            <Package size={48} className="mx-auto text-muted-foreground mb-4" />
            <h2 className="text-xl font-semibold text-foreground mb-2">No orders yet</h2>
            <p className="text-muted-foreground">Start shopping to place your first order</p>
          </div>
        ) : (
          <div className="space-y-4">
            {orders.map((order) => (
              <div key={order.id} className={`rounded-lg border p-6 ${getStatusColor(order.status)}`}>
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-lg font-bold text-foreground">Order #{order.orderNumber}</h3>
                    <p className="text-sm text-muted-foreground">{new Date(order.date).toLocaleDateString("en-IN")}</p>
                  </div>
                  <div className="flex items-center gap-2">
                    {getStatusIcon(order.status)}
                    <span className="font-semibold text-foreground capitalize">{order.status}</span>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                  <div>
                    <p className="text-sm text-muted-foreground mb-1">Items</p>
                    <div className="flex gap-2">
                      {order.items.slice(0, 3).map((item) => (
                        <img
                          key={item.id}
                          src={item.image || "/placeholder.svg"}
                          alt={item.name}
                          className="w-12 h-12 object-cover rounded-lg"
                        />
                      ))}
                      {order.items.length > 3 && (
                        <div className="w-12 h-12 bg-muted rounded-lg flex items-center justify-center text-sm font-semibold text-muted-foreground">
                          +{order.items.length - 3}
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="text-right">
                    <p className="text-sm text-muted-foreground mb-1">Total</p>
                    <p className="text-2xl font-bold text-primary">â‚¹{order.total.toLocaleString("en-IN")}</p>
                  </div>
                </div>

                <div className="border-t border-current pt-3 text-sm">
                  {order.trackingNumber && (
                    <p className="text-muted-foreground">
                      Tracking: <span className="font-semibold text-foreground">{order.trackingNumber}</span>
                    </p>
                  )}
                  {order.estimatedDelivery && (
                    <p className="text-muted-foreground">
                      Estimated Delivery:{" "}
                      <span className="font-semibold text-foreground">{order.estimatedDelivery}</span>
                    </p>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
